import React from "react";
import StripeCheckout from "react-stripe-checkout";

const StripePayment = ({ price }) => {
  const publishedKey = `pk_test_51H07jwGJ39NqQOUFGEm4GiRJ9F1ZjZTdxcl82DhHNiSlHXzjYw3l7xoLAPCU6znwGGbWUc7WcWGkzJ1wbWYpGVT100VccWlWka`;
  const StripePrice = price * 100;
  const Ontoken = (token) => {
    console.log(token);
    alert("Payment Successfully");
  };
  return (
    <StripeCheckout
      name="Online Shopping Ltd."
      label="Pay Now"
      description={`Your Total Price is ${price} `}
      billingAddress
      shippingAddress
      amount={StripePrice}
      stripeKey={publishedKey}
      panelLabel={`Pay Now With ${price}`}
      token={Ontoken}
    />
  );
};

export default StripePayment;
