import React from "react";

import "./custombutton.scss";

const CustomButton = ({
  children,
  isGoogleSignIn,
  Inverted,
  ...otherProps
}) => {
  return (
    <button
      className={`${Inverted ? "Inverted" : ""} ${
        isGoogleSignIn ? "googleSignIn" : ""
      } submit-button`}
      {...otherProps}
    >
      {children}
    </button>
  );
};

export default CustomButton;
