import React from "react";

import CustomButton from "../CustomButtton/Custombutton";
import CartItem from "../cartItem/cartItem";
import { connect } from "react-redux";
import "./cart-dropdown.styles.scss";
import { ToggleCartHidden } from "../../Redux/cart/cart.action";
import { withRouter } from "react-router-dom";

const CartDrop = ({ cartItem, history, dispatch }) => {
  return (
    <div className="cart-dropdown">
      <div className="cart-items">
        {cartItem.length > 0 ? (
          cartItem.map((Item) => <CartItem key={Item.id} item={Item} />)
        ) : (
          <span className="empty-cart">your Cart Item is Empty</span>
        )}
      </div>
      <CustomButton
        onClick={() => {
          history.push("/checkout");
          dispatch(ToggleCartHidden());
        }}
      >
        Go To CheckOut
      </CustomButton>
    </div>
  );
};

const mapStateToProps = ({ cart: { cartItem } }) => ({
  cartItem,
});

export default withRouter(connect(mapStateToProps)(CartDrop));
