import React, { Component } from "react";
import "./App.css";
import Homepage from "./pagess/homepage";
import { Route, Switch, Redirect } from "react-router-dom";

import CheckOut from "./pagess/checkoutPage/checkout";
import ShopPage from "./ShopPage/shopItem/shoppage";
import Header from "./header/header";
import SignInSignOut from "./ShopPage/SiginSigout/SigInSigOut";
import SignUp from './components/SignUp/signUp';
import { createStructuredSelector } from "reselect";
import { selectCurrentUser } from "./Redux/User/user.selector";

import { auth, createUserProfileDocument } from "./firebase/firebase.utils";
import { connect } from "react-redux";
import { setCurrentUser } from "./Redux/User/user.action";

/* Here is Class Based Component to Pass Data Using State Property*/

class App extends Component {
  unsubscribeFromAuth = null;
  componentDidMount() {
    this.unsubscribeFromAuth = auth.onAuthStateChanged(async (userAuth) => {
      const { setCurrentUser } = this.props;
      if (userAuth) {
        const userRef = await createUserProfileDocument(userAuth);
        userRef.onSnapshot((snapShot) => {
          setCurrentUser({
            id: snapShot.id,
            ...snapShot.data(),
          });
        });
      }
      // console.log(userAuth);
      setCurrentUser(userAuth);

      // console.log(collection);

      // AddCollectionAndDocument(
      //   "collectionShop",
      //   collection.map(({ title, items }) => ({ title, items }))
      // );
    });
  }

  componentWillUnmount() {
    this.unsubscribeFromAuth();
  }

  render() {
    const { CurrentUser} = this.props;
    return (
      <div className="App">
        <Header />
        <Switch>
          <Route exact path="/" component={Homepage} />
          <Route path="/shop" component={ShopPage} />
          
          <Route exact path="/checkout" component={CheckOut} />
          <Route
            exact
            path="/signIn"
            render={() =>
              CurrentUser ? <Redirect to="/" /> : <SignInSignOut />
            }
          />
          <Route exact path='/signUp'
            render={() =>
               CurrentUser ? <Redirect to="/" /> 
               : <SignUp/>
            }/> 
            
        </Switch>
      </div>
    );
  }
}
const mapStateToProps = createStructuredSelector({
  CurrentUser: selectCurrentUser,
});

const mapDispatchToProps = (dispatch) => ({
  setCurrentUser: (user) => dispatch(setCurrentUser(user)),
});

export default connect(mapStateToProps, mapDispatchToProps)(App);
