import React from "react";
import {Link} from 'react-router-dom'

import { connect } from "react-redux";
import { createStructuredSelector } from "reselect";
import {
  selectCartItem,
  selectCartItemTotal,
} from "../../Redux/cart/cart.selector";
import {selectCurrentUser} from '../../Redux/User/user.selector'

import CheckoutItem from "../../components/checkoutItem/checkoutItem";
import "./checkout.styles.scss";
import StripePayment from "../../components/StripeComponent/stripe.component";
const CheckOut = ({ cartItems, Total,currentuser}) => {
  return (
    <div className="checkout-page">
      <div className="checkout-header">
        <div className="header-block">
          <span>Product</span>
        </div>
        <div className="header-block">
          <span>Description</span>
        </div>
        <div className="header-block">
          <span>Quantity</span>
        </div>
        <div className="header-block">
          <span>price</span>
        </div>
        <div className="header-block">
          <span>Remove</span>
        </div>
      </div>
      
      {cartItems.map((cartItem) => (
        <CheckoutItem key={cartItem.id} cartItem={cartItem} />
      ))}
      <div className="total">
        <span>Total:${Total}</span>
      </div>
      
      { 
         
        (currentuser) ?
          
          ( Total!==0   ?
            <StripePayment price={Total} /> :
            null
          )
          : 
          <div className='signIn-alert'>
            Before Checkout Please Proceed to
            <Link className='SignOut-Links' to='/signIn'> 
              Login
            </Link>    
          </div>    

      }
       
      
    </div>
  );
};
const mapStateToProps = createStructuredSelector({
  cartItems: selectCartItem,
  Total: selectCartItemTotal,
  currentuser:selectCurrentUser
});

export default connect(mapStateToProps)(CheckOut);
