import shopActionType from "./shop.type";
import {
  firestore,
  ConvertCollectionSnapShotToArray,
} from "../../firebase/firebase.utils";
export const FetchCollectionStart = () => ({
  type: shopActionType.FETCH_COLLECTION_START,
});
export const FetchCollectionSuccess = (ShopCollection) => ({
  type: shopActionType.FETCH_COLLECTION_SUCCESS,
  payload: ShopCollection,
});
export const FetchCollectionFailure = (ErrorMessage) => ({
  type: shopActionType.FETCH_COLLECTION_START,
  payload: ErrorMessage,
});
export const FetchCollectionStartAsync = () => {
  return (dispatch) => {
    dispatch(FetchCollectionStart());
    const CollectionRef = firestore.collection("collectionShop");
    CollectionRef.get()
      .then(async (snapshot) => {
        const CollectionObject = ConvertCollectionSnapShotToArray(snapshot);
        dispatch(FetchCollectionSuccess(CollectionObject));
      })
      .catch((error) => dispatch(FetchCollectionFailure(error.message)));
  };
};
