import React,{useState,Component} from "react";
import "./collectionOverview.scss";
import { connect } from "react-redux";
import { createStructuredSelector } from "reselect";
import { selectCollectionPreview } from "../../Redux/Shop/shop.selector";
import { selectDirectorySection } from "../../Redux/Directory/directory.selector";
import Search from '../Search/Search';
import CollectionPreview from "../CollectionPreview/CollectionPre";

class CollectionOverview extends Component {
  constructor() {
    super();
    this.state = {
       search:''
     }  
 
    }
  render() {
    const { Collections} = this.props;
    const { search } = this.state; 
    console.log(Collections);
     const  Collection=Collections.filter(collection => {
      let { title } = collection;
      console.log(title);
      return collection.title.toLowerCase().includes(search.toLowerCase())
     })
    
    console.log(this.state.search);
    return (
      <div className="collection-overview">
        <div className='search-item'>
          <Search type='search'
            className='search-bar'
            placeholder='search Item'
            handleChange={(e) => this.setState({
              search: e.target.value})
            }
          />
        </div>
        {Collection.map((collection) => (
          <CollectionPreview key={collection.id} collection={collection} />
        ))}
      </div>
    );
  }
};

const mapStateToProps = createStructuredSelector({
  Collections: selectCollectionPreview,
  section: selectDirectorySection,
});

export default connect(mapStateToProps)(CollectionOverview);
