import { createSelector } from "reselect";
const selectCart = (state) => state.cart;

export const ToggleHiddenCart = createSelector(
  [selectCart],
  (cart) => cart.hidden
);

export const selectCartItem = createSelector(
  [selectCart],
  (cart) => cart.cartItem
);

export const selectCartItemCount = createSelector(
  [selectCartItem],
  (cartItem) =>
    cartItem.reduce((accumulate, cartItem) => accumulate + cartItem.quantity, 0)
);
export const selectCartItemTotal = createSelector(
  [selectCartItem],
  (cartItem) =>
    cartItem.reduce(
      (accumulate, cartItem) => accumulate + cartItem.quantity * cartItem.price,
      0
    )
);
