/* connenct is a Higher Order Component that Access from (react-redux) Library */
import { connect } from "react-redux";
import { createStructuredSelector } from "reselect";
import { compose } from "redux";
import { selectIsCollectionFetch } from "../../Redux/Shop/shop.selector";
import CollectionOverview from "./collectionOverview";

import WithSpinner from "../WithSpinner/WithSpinner";
/* Leverage Redux to get Data(Redux Store)*/
const mapStateToProps = createStructuredSelector({
  isLoading: selectIsCollectionFetch,
});
/* Currying Concept */
const CollectionOverviewContainer = compose(
  connect(mapStateToProps),
  WithSpinner
)(CollectionOverview);

export default CollectionOverviewContainer;
