export const createUserProfileDocument = async (userAuth, additionalData) => {
  if (!userAuth) return;

  const userRef = firestore.doc(`user/${userAuth.uid}`);

  const snapshot = await userRef.get();

  if (!snapshot.exists) {
    const { displayName, email } = userAuth;
    const createdAt = new Date();

    try {
      await userRef.set({
        displayName,
        email,
        createdAt,
        ...additionalData,
      });
    } catch (error) {
      console.log("Error in userRef", error.message);
    }
  }

  return userRef;
};

export const AddCollectionAndDocument = async (collectionKey, ObjectToAdd) => {
  const CollectionRef = firestore.collection(collectionKey);

  console.log(CollectionRef);
  const batch = firestore.batch();
  ObjectToAdd.forEach((obj) => {
    const NewDocObj = CollectionRef.doc();
    console.log(obj);
    batch.set(NewDocObj, obj);
  });

  return await batch.commit();
};

export const ConvertCollectionSnapShotToArray = (snapshot) => {
  const CollectionArray = snapshot.docs.map((doc) => {
    return {
      routeName: encodeURI(doc.data().title.toLowerCase()),
      title: doc.data().title.toLowerCase(),
      items: doc.data().items,
      id: doc.id,
    };
  }); 

  return CollectionArray.reduce((accumulate, collection) => {
    accumulate[collection.title] = collection;
    return accumulate;
  }, {});
};

