import { createSelector } from "reselect";

const selectShop = (state) => state.shop;

export const selectShopCollection = createSelector(
  [selectShop],
  (shop) => shop.ShopCollection
);

export const selectCollection = (collectionUrlParams) =>
  createSelector([selectShopCollection], (ShopCollection) =>
    ShopCollection ? ShopCollection[collectionUrlParams] : null
  );

export const selectCollectionPreview = createSelector(
  [selectShopCollection],
  (ShopCollection) =>
    ShopCollection
      ? Object.keys(ShopCollection).map((keys) => ShopCollection[keys])
      : []
);
export const selectIsCollectionFetch = createSelector(
  [selectShop],
  (shop) => shop.isFetching
);
export const selectIsCollectionLoaded = createSelector(
  [selectShop],
  (shop) => !!shop.ShopCollection
);
