import React from "react";

import { connect } from "react-redux";
import { createStructuredSelector } from "reselect";
import { selectCurrentUser } from "../Redux/User/user.selector";
import { ToggleHiddenCart } from "../Redux/cart/cart.selector";
import { Link } from "react-router-dom";
import { ReactComponent as Logo } from "../LogoContainer/crown.svg";
import "./Header.scss";
import CartIcon from "../components/Cart/cart";
import CartDrop from "../components/CartDrop/cartdrop";
import { auth } from "../firebase/firebase.utils";
const Header = ({ currentUser, hidden }) => {
  return (
    <div className="header">
      <Link className="logo-container" to="/">
        <Logo className="logo" />
      </Link>
      <div className="options">
        <Link className="option" to="/shop">
          shop
        </Link>
         {
          currentUser ? (
          <div className="option" onClick={() => auth.signOut()}>
            Sign Out
          </div>
        ) : (
          <Link className="option" to="/signIn">
            SignIn
          </Link>
        )}
        <CartIcon />
      </div>

      {hidden ? null : <CartDrop />}
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  currentUser: selectCurrentUser,
  hidden: ToggleHiddenCart,
});
export default connect(mapStateToProps)(Header);
