import CartActionType from "./cart.type";
import { AddItem, RemoveItem } from "./cart.utils";

const INITIAL_STATE = {
  hidden: true,
  cartItem: [],
};

const CartReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case CartActionType.TOGGLE_CART_HIDDEN:
      return {
        ...state,
        hidden: !state.hidden,
      };
    case CartActionType.ADD_ITEM:
      return {
        ...state,
        cartItem: AddItem(state.cartItem, action.payload),
      };
    case CartActionType.CLEAR_CART_ITEM:
      return {
        ...state,
        cartItem: state.cartItem.filter(
          (cartItem) => cartItem.id !== action.payload.id
        ),
      };
    case CartActionType.REMOVE_CART_ITEM:
      return {
        ...state,
        cartItem: RemoveItem(state.cartItem, action.payload),
      };
    default:
      return state;
  }
};

export default CartReducer;
